
.cssloadCssloadWrap2 {
  width: 263px;
  height: 263px;
  overflow: hidden;
}
.cssloadWrap {
  position: absolute;
  width: 175px;
  height: 175px;
  left: 50%;
  top: 20%;
  margin-top: -1px;
  margin-left: -88px;
}
.cssloadWrap .cssloadOverlay {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 100;
  box-shadow: 0 0 44px 67px rgb(255,255,255) inset;
  -o-box-shadow: 0 0 44px 67px rgb(255,255,255) inset;
  -ms-box-shadow: 0 0 44px 67px rgb(255,255,255) inset;
  -webkit-box-shadow: 0 0 44px 67px rgb(255,255,255) inset;
  -moz-box-shadow: 0 0 44px 67px rgb(255,255,255) inset;
}
.cssloadWrap .cssloadCogWheel {
  position: absolute;
  top: 50%;
  margin-top: -44px;
  width: 88px;
  height: 88px;
}
.cssloadWrap .cssloadCogWheel.cssloadOne {
  left: -11.25px;
  animation: cssloadRotLeft 1s infinite linear;
  -o-animation: cssload-rotLeft 1s infinite linear;
  -ms-animation: cssload-rotLeft 1s infinite linear;
  -webkit-animation: cssload-rotLeft 1s infinite linear;
  -moz-animation: cssload-rotLeft 1s infinite linear;
}
.cssloadWrap .cssloadCogWheel.cssloadOne .cssloadOne:before {
  border: none;
}
.cssloadWrap .cssloadCogWheel.cssloadTwo {
  right: -11.25px;
  margin-top: -42px;
  transform: rotate(196deg);
  -ms-transform: rotate(196deg);
  -webkit-transform: rotate(196deg);
  -moz-transform: rotate(196deg);
  animation: cssload-rotRight 1s infinite linear;
  -o-animation: cssload-rotRight 1s infinite linear;
  -ms-animation: cssload-rotRight 1s infinite linear;
  -webkit-animation: cssload-rotRight 1s infinite linear;
  -moz-animation: cssload-rotRight 1s infinite linear;
}
.cssloadWrap .cssloadCogWheel.cssloadTwo .cssloadOne:before {
  border: none;
}
.cssloadWrap .cssloadCogWheel .cssloadCog {
  position: absolute;
  width: 100%;
  left: 0;
  top: 50%;
}
.cssloadWrap .cssloadCogWheel .cssloadCog:after {
  display: block;
  position: absolute;
  content: ' ';
  border: 4px solid rgb(0,0,0);
  border-left: none;
  height: 16px;
  width: 9px;
  right: -16px;
  top: -7px;
}
.cssloadWrap .cssloadCogWheel .cssloadCog:before {
  display: block;
  position: absolute;
  content: ' ';
  border-left: 4px solid rgb(0,0,0);
  height: 18px;
  width: 7px;
  right: -11px;
  top: -23px;
  transform: rotate(-20deg);
  -o-transform: rotate(-20deg);
  -ms-transform: rotate(-20deg);
  -webkit-transform: rotate(-20deg);
  -moz-transform: rotate(-20deg);
}
.cssloadWrap .cssloadCogWheel .cssloadCog.cssloadOne {
  transform: rotate(-80deg);
  -o-transform: rotate(-80deg);
  -ms-transform: rotate(-80deg);
  -webkit-transform: rotate(-80deg);
  -moz-transform: rotate(-80deg);
}
.cssloadWrap .cssloadCogWheel .cssloadCog.cssloadTwo {
  transform: rotate(-40deg);
  -o-transform: rotate(-40deg);
  -ms-transform: rotate(-40deg);
  -webkit-transform: rotate(-40deg);
  -moz-transform: rotate(-40deg);
}
.cssloadWrap .cssloadCogWheel .cssloadCog.cssloadThree {
  transform: rotate(0deg);
  -o-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
}
.cssloadWrap .cssloadCogWheel .cssloadCog.cssloadFour {
  transform: rotate(40deg);
  -o-transform: rotate(40deg);
  -ms-transform: rotate(40deg);
  -webkit-transform: rotate(40deg);
  -moz-transform: rotate(40deg);
}
.cssloadWrap .cssloadCogWheel .cssloadCog.cssloadFive {
  transform: rotate(80deg);
  -o-transform: rotate(80deg);
  -ms-transform: rotate(80deg);
  -webkit-transform: rotate(80deg);
  -moz-transform: rotate(80deg);
}





